@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
::before,
::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page__body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 1.6;
  background-color: #ffffff;
  color: #000000;
  display: flex;
  flex-direction: column;
  min-width: 320px;
}

.main-page {
  flex-grow: 1;
}

.main-page__about {
  margin-bottom: 58px;
}
@media (min-width: 768px) {
  .main-page__about {
    margin-bottom: 84px;
  }
}
@media (min-width: 1150px) {
  .main-page__about {
    margin-bottom: 88px;
  }
}

@media (min-width: 1150px) {
  .main-page__reviews {
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  .main-header--js-menu {
    min-height: 79px;
  }
}
@media (min-width: 768px) {
  .main-header {
    background-color: #fafafa;
  }
}
@media (min-width: 1150px) {
  .main-header {
    overflow-x: hidden;
  }

  .main-header__nav {
    margin: 0 auto;
  }
}
.main-nav {
  font-weight: 700;
  font-size: 17px;
  line-height: 1.41;
  color: #000000;
  background-color: #ffffff;
}

@media (max-width: 767px) {
  .main-nav--opened {
    position: absolute;
    width: 100%;
    min-width: 320px;
    z-index: 100;
  }
}

@media (max-width: 767px) {
  .main-nav--closed {
    position: absolute;
    width: 100%;
    min-width: 320px;
    z-index: 100;
  }
  .main-nav--closed .main-nav__primary-list,
.main-nav--closed .main-nav__user-nav {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--no-js {
    display: block;
    position: static;
  }
  .main-nav--no-js .main-nav__primary-list,
.main-nav--no-js .main-nav__user-nav {
    display: block;
  }
}

.main-nav__toogle {
  cursor: pointer;
  padding: 0 15px;
  border: none;
  background-color: #ffffff;
  position: absolute;
  top: 39px;
  right: -9px;
  z-index: 2;
  height: 50px;
  transform: translate(-50%, -50%);
}

.main-nav__toogle--no-js {
  display: none;
}

.main-nav__toogle-icon {
  display: block;
  width: 20px;
  background-color: #000000;
  position: relative;
}
.main-nav__toogle-icon::before, .main-nav__toogle-icon::after {
  content: "";
  position: absolute;
  background-color: #000000;
}

.main-nav__toogle--closed .main-nav__toogle-icon {
  height: 2px;
  transition: 0.2s;
}
.main-nav__toogle--closed .main-nav__toogle-icon::before, .main-nav__toogle--closed .main-nav__toogle-icon::after {
  width: 20px;
  height: 2px;
  left: 0;
  top: 6px;
  transition: 0.2s;
}
.main-nav__toogle--closed .main-nav__toogle-icon::after {
  top: -6px;
}

.main-nav__toogle--opened .main-nav__toogle-icon {
  height: 0;
}
.main-nav__toogle--opened .main-nav__toogle-icon::before, .main-nav__toogle--opened .main-nav__toogle-icon::after {
  width: 27px;
  height: 2px;
  left: -3px;
  top: -1px;
  transform: rotate(45deg);
  transition: 0.2s;
}
.main-nav__toogle--opened .main-nav__toogle-icon::after {
  transform: rotate(-45deg);
}

.main-nav__logo-wrapper {
  display: flex;
  justify-content: center;
  padding: 22px 30px 16px;
  border-bottom: 1px solid #ececec;
}

.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.3;
}

.main-nav__primary-list,
.main-nav__user-nav,
.main-nav__secondary-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-nav__secondary-list {
  display: none;
}

.main-nav__item {
  border-bottom: 1px solid #ececec;
}

.main-nav__link {
  color: inherit;
  text-decoration: none;
  display: block;
  padding: 27px 33px 27px 76px;
  position: relative;
}
.main-nav__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__link:hover .main-nav__icon {
  color: #63d1bb;
}
.main-nav__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-nav__link:active .main-nav__icon {
  color: rgba(99, 209, 187, 0.3);
}

.main-nav__icon {
  position: absolute;
  top: 50%;
  left: 32px;
  transform: translateY(-50%);
  color: rgba(0, 0, 0, 0.2);
}

.main-nav__info {
  display: none;
}

.main-nav__info-link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-nav__info-link:active {
  color: rgba(0, 0, 0, 0.3);
}

@media (min-width: 768px) {
  .main-nav__toogle {
    display: none;
  }

  .main-nav {
    display: grid;
    grid-template-columns: 1fr auto;
    background-color: transparent;
  }

  .main-nav__logo-wrapper {
    background-color: #ffffff;
    display: flex;
    justify-content: start;
    padding: 0;
    padding-top: 22px;
    padding-left: 49px;
    padding-bottom: 14px;
    min-width: 206px;
  }

  .main-nav__logo {
    width: 113px;
    height: 46px;
  }

  .main-nav__primary-list,
.main-nav__user-nav {
    display: flex;
    flex-wrap: wrap;
  }

  .main-nav__primary-list {
    font-weight: 700;
    font-size: 15px;
    line-height: 1.6;
    color: #000000;
    padding-left: 29px;
    grid-area: 2/1/3/-1;
    max-width: 450px;
  }

  .main-nav__user-nav {
    background-color: #ffffff;
    grid-area: 1/2/2/-1;
    align-self: center;
    border-bottom: 1px solid #ececec;
    justify-content: flex-end;
    padding-right: 50px;
  }

  .main-nav__item {
    border-bottom: none;
  }

  .main-nav__user-nav .main-nav__item {
    border-left: 1px solid #ececec;
  }

  .main-nav__primary-list .main-nav__link {
    padding: 21px 15px 21px 21px;
  }

  .main-nav__user-nav .main-nav__link {
    padding: 34px 0 30px;
  }
  .main-nav__user-nav .main-nav__link--search {
    padding-left: 32px;
    padding-right: 31px;
  }
  .main-nav__user-nav .main-nav__link--card {
    padding-left: 33px;
  }

  .main-nav__icon {
    position: static;
    transform: translateY(0%);
  }

  .main-nav__text--search {
    display: none;
  }
  .main-nav__text--card {
    margin-left: 25px;
  }

  .main-nav__info {
    font-weight: 400;
    font-size: 15px;
    line-height: 1.6;
    color: #444444;
    display: block;
    grid-area: 2/2/3/3;
    justify-self: end;
    max-width: 350px;
    margin: 0;
    padding-right: 50px;
    text-align: end;
  }

  .main-nav__info-link {
    color: inherit;
    text-decoration: none;
    display: block;
    padding: 20px 0;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    width: 1050px;
    grid-template-columns: 440px auto 1fr;
  }

  .main-nav__logo-wrapper {
    grid-area: 1/2/2/3;
    min-width: 169px;
    padding: 0;
    padding-top: 9px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .main-nav__logo {
    width: 149px;
    height: 72px;
  }

  .main-nav__primary-list {
    font-weight: 700;
    font-size: 17px;
    line-height: 1.41;
    color: #000000;
    grid-area: 1/1/2/2;
    background-color: #ffffff;
    padding: 0;
    position: relative;
    border-bottom: 1px solid #ececec;
  }
  .main-nav__primary-list::before {
    content: "";
    position: absolute;
    background-color: #ffffff;
    height: calc(100% + 1px);
    width: calc(50vw - 100%);
    top: 0;
    right: 100%;
    border-bottom: 1px solid #ececec;
  }

  .main-nav__user-nav {
    grid-area: 1/3/2/4;
    padding-right: 2px;
    align-self: auto;
    position: relative;
    border-bottom: 1px solid #ececec;
  }
  .main-nav__user-nav::before {
    content: "";
    position: absolute;
    background-color: #ffffff;
    height: calc(100% + 1px);
    width: calc(50vw - 100%);
    top: 0;
    left: 100%;
    border-bottom: 1px solid #ececec;
  }

  .main-nav__secondary-list {
    font-weight: 700;
    font-size: 15px;
    line-height: 1.6;
    color: #000000;
    display: flex;
    grid-area: 2/1/3/3;
    max-width: 609px;
    flex-wrap: wrap;
  }

  .main-nav__info {
    grid-area: 2/3/3/4;
    padding: 0;
  }

  .main-nav__primary-list .main-nav__item:not(:last-child) {
    margin-right: 71px;
  }

  .main-nav__primary-list .main-nav__link {
    padding: 34px 0 30px;
  }

  .main-nav__user-nav .main-nav__item {
    align-self: start;
  }

  .main-nav__secondary-list .main-nav__item:not(:last-child) {
    margin-right: 62px;
  }

  .main-nav__secondary-list .main-nav__link {
    padding: 21px 0 16px;
  }

  .main-nav__item--decorate {
    position: relative;
  }

  .main-nav__item--decorate::before {
    position: absolute;
    content: "";
    width: 1px;
    height: 100%;
    background-color: #ececec;
    right: -36px;
  }
}
